module.exports = ".your .card {\n  bottom: 0;\n  position: absolute;\n}\n\n.your .ten {\n  left: 0;\n}\n\n.your .jack {\n  left: 20px;\n}\n\n.your .queen {\n  left: 40px;\n}\n\n.your .king {\n  left: 60px;\n}\n\n.your .ace {\n  left: 80px;\n}\n\nbody {\n  background-color: green;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.table {\n  width: 500px;\n  height: 200px;\n  border: 2px solid black;\n  position: relative;\n  background-size: 50px 50px;\n  background-image:\n    linear-gradient(to right, grey 1px, transparent 1px),\n    linear-gradient(to bottom, grey 1px, transparent 1px);\n}\n\n.card {\n  width: 73px;\n  height: 97px;\n  background-image: url(\"/images/sprites/cards.png\");\n}\n\n.nine {\n  background-position: calc(100% * 8 / 12) calc(100% * 2 / 3);\n}\n\n.ten {\n  background-position: calc(100% * 9 / 12) calc(100% * 2 / 3);\n}\n\n.jack {\n  background-position: calc(100% * 10 / 12) calc(100% * 2 / 3);\n}\n\n.queen {\n  background-position: calc(100% * 11 / 12) calc(100% * 2 / 3);\n}\n\n.king {\n  background-position: 100% calc(100% * 2 / 3);\n}\n\n.ace {\n  background-position: 0 calc(100% * 2 / 3);\n}\n\n.opponent {\n  display: flex;\n}\n\n.opponent.deck {\n  z-index: 10;\n  position: relative;\n}\n\n.opponent .card {\n  margin-right: 27px;\n  flex-shrink: 0;\n}"